import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Homepage from './pages/Homepage';
import PortfolioDemandAI from './components/PortfolioDemandAI';
import PortfolioCekOngkir from './components/PortfolioCekOngkir';
import PortfolioMelisa from './components/PortfolioMelisa';

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path="/">
          <Homepage />
        </Route>
        <Route path="/portfolio/demandai">
          <PortfolioDemandAI />
        </Route>
        <Route path="/portfolio/cekongkir">
          <PortfolioCekOngkir />
        </Route>
        <Route path="/portfolio/melisa">
          <PortfolioMelisa />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
